.roadmap-wrapper {
  padding: 0 80px;
  background-image: url("../../assets/img/stars.svg");
  @media screen and (max-width: 916px) {
    padding: 0 40px;
  }
}

.roadmap-content {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: 860px) {
    display: none;
  }
}

.roadmap-container {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  justify-content: space-around;
  padding-bottom: 80px;

  > div:not(:last-child) {
    margin-bottom: 48px;
    @media screen and (max-width: 680px) {
      margin-bottom: 20px;
    }
  }
}

.roadmap-block {
  padding: 25px;
  width: 250px;
  background: #1c1c1c;
  border-radius: 30px;
  text-align: center;
  font-family: "Sharpie", sans-serif;
  position: relative;
  @media screen and (max-width: 1050px) {
    padding: 20px;
  }
  @media screen and (max-width: 680px) {
    padding: 8px;
  }
}

.hidden-right:before {
  content: " ";
  position: absolute;
  width: 30vw;
  height: 1px;
  background-color: #494949;
  right: -30vw;
  top: 50%;

  @media screen and (max-width: 1319px) {
    width: 20vw;
    right: -20vw;
  }

  @media screen and (max-width: 574px) {
    display: none;
  }
}

.hidden-left:before {
  content: " ";
  position: absolute;
  width: 30vw;
  height: 1px;
  background-color: #494949;
  left: -30vw;
  top: 50%;

  @media screen and (max-width: 1319px) {
    width: 20vw;
    left: -20vw;
  }
}

.roadmap-header {
  font-style: italic;
  font-weight: 500;
  font-size: 48px;
  line-height: 63px;
  font-family: "Sharpie-Regular", sans-serif;

  @media screen and (max-width: 1050px) {
    font-size: 36px;
  }
  @media screen and (max-width: 680px) {
    font-size: 28px;
  }
}

.roadmap-description {
  font-size: 18px;
  line-height: 21px;
  @media screen and (max-width: 1050px) {
    font-size: 16px;
  }
  @media screen and (max-width: 680px) {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.roadmap-rocket {
  width: 206px;
  height: auto;
  position: relative;
  z-index: 2;

  @media screen and (max-width: 1050px) {
    width: 170px;
  }

  @media screen and (max-width: 860px) {
    width: 270px;
    margin-top: 60px;
  }

  @media screen and (max-width: 680px) {
    width: 200px;
  }
}

.roadmap-rocket-container {
  width: fit-content;
  height: fit-content;
  position: relative;

  @media screen and (max-width: 574px) {
    display: none;
  }
}

.roadmap-rocket-container:before {
  content: " ";
  background: black;
  width: 145px;
  height: 100%;
  position: absolute;
  left: 40px;
  z-index: 1;
  @media screen and (max-width: 1050px) {
    width: 125px;
  }
  @media screen and (max-width: 680px) {
    left: 20px;
    width: 165px;
  }
}

.roadmap-rocket-container:after {
  content: " ";
  background: black;
  width: 210px;
  height: 400px;
  position: absolute;
  left: 0;
  z-index: 1;
  top: 30%;
  @media screen and (max-width: 1050px) {
    width: 165px;
  }
}

.roadmap-content-mobile {
  display: none;
  @media screen and (max-width: 860px) {
    display: flex;
    justify-content: space-between;
  }
  @media screen and (max-width: 574px) {
    justify-content: center;
  }
}
