.about-container {
  padding: 6% 97px 0 97px;

  @media screen and (max-width: 966px) {
    padding: 4% 60px 0 60px;
  }
  @media screen and (max-width: 761px) {
    padding: 2% 40px 0 40px;
  }
}

.about-block {
  grid-template-columns: 1fr 1fr;
  column-gap: 20%;
  margin-bottom: 100px;

  @media screen and (max-width: 761px) {
    // grid-template-rows: 1fr;
    grid-auto-flow: dense;
    grid-template-columns: 1fr;
  }
}

.about-text {
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 1.5;
}

.about-text-headline {
  font-style: italic;
  line-height: 1.2;
  font-family: "Sharpie-Bold", sans-serif;
  font-size: 36px;
}

.about-text-description {
  color: #cccccc;
  margin-bottom: 10px;
}
