.welcome-container {
  width: fit-content;
  height: fit-content;
  position: relative;
  margin-top: 60px;
}

.links-wrapper {
  position: absolute;
  height: 100%;
  display: flex;

  @media screen and (max-width: 966px) {
    width: 100%;
  }
}

.links-container {
  margin: auto;
  padding: 0 97px;
  display: flex;
  flex-direction: column;
  z-index: 1;

  @media screen and (max-width: 966px) {
    width: 100%;
    top: 35%;
    padding: 0 97px;
    align-items: center;
  }

  @media screen and (max-width: 620px) {
    top: 30%;
    align-items: center;
  }

  @media screen and (max-width: 520px) {
    top: 25%;
    align-items: center;
  }
}

.welcome-title {
  text-transform: uppercase;
  font-size: 54px;
  font-weight: 700;
  font-style: italic;
  max-width: 340px;
  margin-bottom: 10px;

  font-family: "Sharpie-Bold", sans-serif;
  @media screen and (max-width: 966px) {
    font-size: 1.4em;
  }
  @media screen and (max-width: 620px) {
    font-size: 1.2em;
  }
  @media screen and (max-width: 520px) {
    font-size: 1em;
  }
}

.banner {
  width: 100%;
  @media screen and (max-width: 966px) {
    opacity: 0.6;
  }
}

.links {
  display: flex;
  > a:not(:last-child) {
    margin-right: 15px;
  }
  img {
    width: 60px;
  }
}
