.header-container {
  background-color: black;
  height: 60px;
  position: fixed;
  width: 100%;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
  padding: 0 97px;

  @media screen and (max-width: 966px) {
    justify-content: space-between;
    padding: 0 20px;
  }
}

.header-items-container {
  display: flex;
  align-items: center;

  > *:not(:last-child) {
    margin-right: 20px;
  }

  @media screen and (max-width: 966px) {
    position: fixed;
    height: 100%;
    width: 100%;
    background: black;
    top: -100%;
  }
}

.header-item {
  cursor: pointer;
  text-decoration: none;
  color: white;
  transition: color 200ms ease-in-out;
}
.header-item:hover {
  color: lightgray;
}

.header-title {
  font-style: italic;
  font-size: 26px;
}

.header-logo {
  margin-right: 5px;
  height: 45px;
}

.header-button {
  display: none;
  @media screen and (max-width: 966px) {
    display: block;
  }
  #menu__toggle {
    opacity: 0;
  }

  .menu__btn {
    display: flex;
    align-items: center;
    position: fixed;
    top: 20px;
    right: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
    user-select: none;
  }

  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: white;
  }
  .menu__btn > span::before {
    content: "";
    top: -8px;
  }
  .menu__btn > span::after {
    content: "";
    top: 8px;
  }
  .menu__box {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100vw;
    height: 100%;
    margin: 0;
    list-style: none;
    text-align: center;
    background-color: black;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
    transition: top 200ms ease-in-out;
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .header-item {
      font-size: 32px;
    }

    > li:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-family: "Roboto", sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu__item:hover {
    background-color: #cfd8dc;
  }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    top: 0;
  }
}
