.d-flex {
  display: flex;
}

.columnReverse {
  @media screen and (max-width: 761px) {
    flex-direction: column-reverse;
  }
}

.d-grid {
  display: grid;
}

.center {
  align-items: center;
  justify-content: center;
}

.img {
  max-width: 400px;
  max-height: 400px;
  min-width: 250px;
  width: 100%;
}

.marginTop {
  margin-top: 60px;
}

.icon-wrapper {
  background: #292929;
  border-radius: 100%;
  padding: 12px;
  width: 46px;
  height: 46px;
  display: flex;

  > img {
    width: 22px;
    margin: auto;
  }
}

.small {
  width: 38px;
  height: 38px;
  padding: 0;

  > img {
    width: 16px;
  }
}
