.footer-wrap {
  padding: 10px 97px;
}

.footer {
  align-items: center;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: 772px) {
    flex-direction: column;
  }
}

.footer-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.footer-link {
  text-decoration: underline;
  margin-right: 20px;
}

.footer-right {
  display: flex;
  align-items: center;
  @media screen and (max-width: 465px) {
    flex-direction: column;
    > * {
      margin-top: 20px;
    }
  }
}
